<template>
<div class="app-update">
    <!-- 表头 -->
  <mt-header title="添加收货地址">
  <router-link :to="'/arr'" slot="left">
    <mt-button icon="back"></mt-button>
  </router-link>
</mt-header>
    <!-- 添加住址 -->
<div class="mui-card">
    <div class="mui-card-content">
        <div class="mui-card-content-inner">
            姓名:<input type="text" placeholder="请输入2-7个字符的汉字姓名" v-model="name" value="123">
            电话:<input type="text"  placeholder="请输入您正确的电话格式" v-model="tel">
            住址:<input type="text"  placeholder="请输入您的详细地址" v-model="place">
            <button @click="update">确认修改</button>
        </div>
    </div>
</div>
</div>  
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
       return{
            id:this.$route.query.id,
           addrlist:{},//住址
            name:'',
            tel:'',
            place:''
       }
    },
    created(){
        this.load()
    },
    methods:{
        // 加载当前用户的地址
        load(){
            var url="addr?id="+this.id;
            this.axios.get(url).then(result=>{
                // console.log(result.data);
                this.addrlist=result.data.data[0];
                console.log(this.addrlist);
                this.name=this.addrlist.name;
                this.tel=this.addrlist.tel;
                this.place=this.addrlist.place;
            })
        },
        update(){
            var name=this.name;
            var tel=this.tel;
            var place=this.place;
            console.log(name+":"+tel+":"+place+":");
            var reg1=/^[\u4e00-\u9fa5]{2,7}$/;
            if(!reg1.test(name)){
                Toast("您的姓名格式不正确");
                return;
            }
            var reg2=/^((\+86|0086)\s+)?1[3-8]\d{9}$/;
            if(!reg2.test(tel)){
                Toast("您的手机格式不正确");
                return;
            }
            var postData=this.qs.stringify({
                id:this.id,
                name:name,
                tel:tel,
                place:place
            })
            console.log(postData);
            var url="updater?id="+this.id;
            console.log(this.id);
            this.axios.post(url,postData).then(result=>{
                console.log(result.data.code);
                if(result.data.code==1){
                    Toast("修改成功");
                    this.$router.push("/arr");
                }
            })
        }
    }
}
</script>
<style scoped>
/*表头*/
.mint-header{
background: rgba(0, 0, 0, 0.1);
}
.mui-card-content-inner input{
    border-radius: 50px 50px 50px 50px;
}
button{
    width: 100%;
     background: #bed5ab;
     color: #fff;
}
</style>



