<template>
     <div class="add">
    <!--Template-->
    <button @click="change(-1)">-</button>
    <span>{{n}}</span>
    <button @click="change(+1)">+</button>
  </div>
</template>
<script>
export default {
    name:"add",
    data(){
        return {
            n:1
        }
    },
    methods:{
        change(i){this.n+=i;}
    }
}
</script>
<style scoped>
button{
    padding: 2px 6px;
    width: 30px;
}
span{
    width: 30px;
    position: relative;
    top: 2px;
    padding: 2px 25px;
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
}
</style>



