<template>
<div class="app-goodinfo">
    <!-- 表头 -->
  <router-link :to="'/goodlist'" slot="left">
    <mt-button class="btn">
        <span class="mint-button-icon">
            <i class="mintui mintui-back"></i>
        </span> 
    </mt-button>
  </router-link>
  <!-- 轮播/狗粮图片 -->
    <div class="mui-card">
         <div class="mui-card-header mui-card-media" >
            <mt-swipe :auto="4000" class="mt-swipe">
           <mt-swipe-item v-for="(item,i) of imgs" :key="i">
               <img :src="''+item.pic">
               </mt-swipe-item>
       </mt-swipe>
        </div> 
        <!-- 狗\猫粮标题价格 -->
        <div class="mui-card-content">
            <div class="mui-card-content-inner">
                <p class="price">¥ <span>{{info.price}}</span> </p>
                <h4>{{info.title}}</h4>
                <p class="num">库存量:999</p>
            </div>
        </div>
        <!-- 商品详情栏 -->
            <table>
                <thead>
                    <th colspan="2">商品详情</th>   
                </thead>
                <tbody>
                    <tr>
                        <td>所属品牌:</td>
                        <td>{{info.subtitle}}</td>
                    </tr>
                    <tr>
                        <td>产品规格:</td>
                        <td>{{info.spe}}</td>
                    </tr>
                    <tr>
                        <td>配方:</td>
                        <td>{{info.formula}}</td>
                    </tr>
                    <tr>
                        <td>使用年龄:</td>
                        <td>{{info.applylicable}}</td>
                    </tr>
                </tbody>
            </table>
            <ul>
                <li v-for="(item,i) of pics" :key="i">
                    <img :src="''+item.pic">
                </li>
            </ul>
        <div class="bottom">
            <!-- 首页 -->
        <router-link :to="'/index'">
            <mt-button class="return home">
                <span class="mui-icon mui-icon-home"></span>
                <p>首页</p>
                </mt-button>
         </router-link>
         <!-- 购物车 -->
        <router-link :to="'/shopcart'">
            <mt-button class="return">
                <span class="mui-icon mui-icon-extra mui-icon-extra-cart"></span>
                <p>购物车</p>
                </mt-button>
         </router-link>
            <!-- 购物车 -->
            <mt-button class="shopcart" @click="addcart">加入购物车</mt-button>
            <mt-button class="shopping" @click="shop">立即购买</mt-button>
        </div>   
    </div>
</div>
</template>
<script>
import {MessageBox} from 'mint-ui'
import {Toast} from "mint-ui"
export default {
    data(){
        return{//上个组件传递的lid
            lid:this.$route.query.lid,
            imgs:[],//相对应商品的轮播
            info:{},//显示商品对象
            pics:{} //对应商品的详情页
        }
    },
    created(){
        this.foodlist();
    },
    methods:{
        // 立即购买
        shop(){
            MessageBox('下单成功', '自动跳转订单页');
            this.$router.push("/Merorder");
        },
        // 点击加入购物车
        addcart(){
            // console.log(1111);
            // 获取当前加入购物车的商品相关信息
            var pid=this.lid;
            var pname=this.info.title;
            var price=this.info.price;
            var pic=this.info.img;
            // 发送ajax
            var url="addcart?pid="+pid+"&price="+price+"&pname="+pname+"&pic="+pic;
            // 获取返回结果
            this.axios.get(url).then(result=>{
                // console.log(result.data.code);
                if(result.data.code==1){
                    Toast("加入成功,请前往购物车支付")
                    //详见main.js
                    //点击显示购物车的数量相加
                     this.$store.commit("increment");
                }
                
            })
        },
        // 加载当前商品详情信息
        foodlist(){
            //详情页轮播
            var url="lunbo?lid="+this.lid;
            this.axios.get(url).then(result=>{
                // console.log(result.data.data)
                this.imgs=result.data.data;
                // console.log(this.imgs);
            })
            //详情页内容
            var url="comment?lid="+this.lid;
            this.axios.get(url).then(result=>{
                // console.log(result.data.data)
                 this.info=result.data.data[0];
                //  console.log(this.info);
            })
            //详情页详情
            var url="foodPic?lid="+this.lid;
            this.axios.get(url).then(result=>{
            // console.log(result.data.data)
                this.pics=result.data.data;
                console.log(this.pics);
            })
        }
    }
}
</script>
<style scoped>
/*表头*/
.btn{
background: rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    position: fixed;
    top: 3px;
    left: 3px;
    z-index: 10;
    color: #fff;
}
/*轮播*/
.mint-swipe{
        height: 400px;
    }
.mint-swipe img{
        width: 100%;
    }
/**表头下面的布局*/
.mui-card{
    margin: 0;
}
.mui-card-header.mui-card-media{
    padding: 0;
}
/*标题*/
.price{
    font-size: 20px;
    color: #d68c05;
}
.price span{
    font-size: 28px;
}
/*标题旁边的库存*/
.num{
    position: absolute;
    top: 45px;
    right: 20px;
}
/*table  列表 详情页*/
table{
    margin: 0 auto;
    border: 1px solid #c8c7cc;
    width: 98%;
    box-shadow: 0px 1px 10px 1px #d2d1d3;

}
table thead th{
    text-align: center;
    padding-bottom:5px; 
}
table tbody tr td:nth-child(2n-1){
    text-align: center
}
/*详情页图片*/
ul{
    padding: 0;
}
ul li{
    margin-top: -6px;
}
ul li img{
    width: 100%;
}
/*底部样式*/
.bottom{
    position: fixed;
    bottom: 0;
    background: #fff;
    width: 100%;
    height: 55px;
}
/*返回首页*/

.return{
    position: fixed;
    bottom: 0;
    left: 18%;
    border: 0px;
    width: 20%;
    height: 55px;
    padding: 0;
    font-size: 14px;
    background: #fff;
}
.return span{
    position: relative;
    top: 2px;
}
.return p{
    position: relative;
    top: 3px;
}
.home{
    left: 0;
}
/*购物车按钮*/
.shopcart,.shopping{
    position: fixed;
    bottom:0;
    width: 30%;
    height: 55px;
    color: #fff;
}
.shopcart{
    left: 40%;
    background:#bed5ab;  
}
    /* 立即购买 */
.shopping{
    right: 0px;
    background: #d68c05;
}
</style>



