<template>
    <div class="app-goodlist">
         <div class="search">
             <input type="text" placeholder="请输入您想要的商品" v-model="title">
             <mt-button class="btn" @click="cha">搜索</mt-button>
         </div>
         <!-- 商品列表 -->
         <div class="goods">
            <div class="goods-item" v-for="(item,i) of list" :key=i>
                <img :src="''+item.img" @click.prevent="jumpInfo" :data-lid="item.lid">
                <div class="font">
                    <span>{{item.title}}</span>
                    </div>
                <div class="info">
                    <span>售价:</span>
            <span class="now" >{{item.price}}</span>
                </div>
            </div>
        </div>   
            <!-- 加载更多 -->
            <mt-button size="large" @click="loadMore" class="more">+</mt-button>
    <!-- 底部 -->
<nav class="mui-bar mui-bar-tab">
    <router-link class="mui-tab-item " :to="'/Index'">
        <span class="mui-icon mui-icon-home"></span>
        <span class="mui-tab-label">首页</span>
    </router-link>
    <router-link class="mui-tab-item mui-active" :to="'/Goodlist'">
        <span class="mui-icon mui-icon-extra mui-icon-extra-cate"></span>
        <span class="mui-tab-label">商品</span>
    </router-link>
    <router-link class="mui-tab-item" :to="'/shopcart'">
        <span class="mui-icon mui-icon-extra mui-icon-extra-cart">
            <span class="mui-badge">
                {{$store.getters.optCartCount}} 
            </span>
        </span>
        <span class="mui-tab-label ">购物车</span>
    </router-link>
    <router-link class="mui-tab-item" :to="'/self'">
        <span class="mui-icon mui-icon-contact"></span>
        <span class="mui-tab-label">个人中心</span>
    </router-link>
</nav>


</div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return{
            list:[],//模板中的商品列表
            pno:0,   //当前的页数
            btn:"",  //判断列表是否有商品
            title:""
        }
    },
    created(){
        //自动加载商品列表
        this.loadMore();
    },
    methods:{
        // 跳转到详情页
        jumpInfo(e){
            var lid=e.target.dataset.lid;
            // console.log(lid); 
            this.$router.push("/GoodInfo?lid="+lid);
        },
        // 加载列表
        loadMore(){
            this.pno++;
            var url="goodlist?pno="+this.pno;
            this.axios.get(url).then(result=>{
                console.log(result.data.data)
                // 使用concat拼接数组
                // this.list=result.data.data;
        var rows=this.list.concat(result.data.data);
                this.list=rows;
                console.log(this.list);
            })
        },
        cha(){
             this.list=[];
             var url="search?title="+this.title;
            this.axios.get(url).then(result=>{
                // console.log(result.data.data)
                var arr=result.data.data;
                if(arr.length==0){
                    Toast("很抱歉,您搜索的未找到")
                }else{
                    this.list=arr;
                    // console.log(this.list);
                } 
            })
        }
    }
}
</script>
<style scoped>
/* 修改点击下面的导航颜色 */
    .mui-bar-tab .mui-tab-item.mui-active{
        color: #bed5ab;
    }
    /* 购物车泡泡颜色 */
    .mui-icon .mui-badge,.btn{
        background: #bed5ab;
        color: #fff;
    }
/*搜索框*/
.search{
    width: 100%;
}
.search input{
    width: 70%;
    height: 30px;
    margin-top: 5px;
    margin-left: 20px;
    margin-right: 10px;
    margin-bottom: 0;
    border-radius: 100px;
}
.btn{
    width: 60px;
    height: 30px;
    margin-top: 5px;
}
    #bg{
        padding: 0;
       
    }
    /* 每个列表上的样式 */
    .goods{
        display: flex;
        flex-wrap: wrap;
        
    }
    .goods-item{
        width: 46%;
        margin: 10px 10px;
        border-radius: 5px;
         box-shadow: 0px 1px 5px 4px #d2d1d3;
         position: relative;
    }
    .goods-item:nth-child(odd){
        margin-right: 0;
    }
    .goods-item img{
         width:100%;
    }
    .font{
        width: 100%;
        position: absolute;
        top: 75%;
        background: #bed5ab;
        color: #fff;
        font-size: 13px;
        font-weight: 500;
    }
    .font{
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }
    .info{
        padding-left: 5px;
        padding-bottom:5px;
        font-size: 16px;
    }
    .now{
        color: #d68c05;
    }
    /* 加载更多 */
    .more{
        background: #bed5ab;
        color: #fff;
        font-size: 40px;
        line-height: 35px;
    }
</style>



