<template>
<div class="app-shopcart">
    <!-- 表头 -->
<mt-header title="购物车"></mt-header>
    <!-- 装饰三角 -->
<div class="decorate"></div>
    <!-- 订单地址 -->
<div class="mui-card">
    <router-link to='/arr'>
    <div class="mui-card-content">
        <div class="mui-card-content-inner">
            <div class="mui-card-content-inner">
                <p>
                    <span>{{addrlist.name}}  </span>
                    <span>{{addrlist.tel}}</span>
                </p>
                <p>{{addrlist.place}}</p>
            </div>
        </div>
    </div>
    </router-link>
</div>
    <!-- 购物车列表 -->
<ul class="mui-table-view">
    <li class="mui-table-view-cell mui-media" v-for="(item,i) of shoplist" :key=i>
        <input type="checkbox" class="check" :checked="item.cb" @click="modifyItem" :data-id="item.id" :data-i="i">
        <img class="mui-media-object mui-pull-left" :src="''+item.pic">
        <div class="mui-media-body">
            <p class="title">{{item.pname}}</p>
            <p class='mui-ellipsis'>¥{{item.price}}</p>
            <my-add></my-add>
            <span class="mui-icon mui-icon-trash del" :data-idx="i" :data-id="item.id" @click="delItem"></span>

        </div>
    </li>
</ul>
    <!-- 提交订单 -->
    <div class="order">
        <div>
        <input type="checkbox" @click="selectAll" :checked="allcb">全选
        </div>
        <div>
            <span class="font">合计:{{total.toFixed(2)}}</span>  
            <mt-button @click="mes">结算</mt-button></div>
        
    </div>
    <!-- 底部 -->
<nav class="mui-bar mui-bar-tab">
    <router-link class="mui-tab-item " :to="'/Index'">
        <span class="mui-icon mui-icon-home"></span>
        <span class="mui-tab-label">首页</span>
    </router-link>
    <router-link class="mui-tab-item" :to="'/Goodlist'">
        <span class="mui-icon mui-icon-extra mui-icon-extra-cate"></span>
        <span class="mui-tab-label">商品</span>
    </router-link>
    <router-link class="mui-tab-item mui-active" :to="'/shopcart'">
        <span class="mui-icon mui-icon-extra mui-icon-extra-cart">
            <span class="mui-badge">
                {{$store.getters.optCartCount}} 
            </span>
        </span>
        <span class="mui-tab-label ">购物车</span>
    </router-link>
    <router-link class="mui-tab-item" :to="'/self'">
        <span class="mui-icon mui-icon-contact"></span>
        <span class="mui-tab-label">个人中心</span>
    </router-link>
</nav>
</div>
</template>
<script>
//弹出框
import {MessageBox} from 'mint-ui'
import {Toast} from "mint-ui"
export default {
    data(){
        return{
            addrlist:[],//住址
            shoplist:[],//加入购物车的列表
            allcb:false,
            id:1
        }
    },
    created(){
        this.addr()
        this.loadMore()
    },
    //计算总价
    computed:{
        total(){
            var sum=0;
            for(var p of this.shoplist){
                if(this.allcb){
                sum+=p.price;
                }
            }
            return sum;
        }
    },
    methods:{
        //点击删除当前商品
        delItem(e){
            //1.获取购物车商品id
            var id=e.target.dataset.id;
            // var id=this.id;
            console.log(id);
            var idx =e.target.dataset.idx;
            //2.创建url
            var url="removeItem";
            var postData=this.qs.stringify({
                id:id,
            });
            // console.log(postData);
            this.axios.post(url,postData).then(result=>{
                if(result.data.code==1){
                    this.shoplist.splice(idx,1)
                    Toast('删除成功');
                    this.$store.commit("reduce");
                }else{
                    Toast('删除失败');
                }
            })
        },
        //结算成功
        mes(){
            if(this.allcb){
                MessageBox('下单成功', '自动跳转订单页');
                this.shoplist='';
                this.$router.push("/Merorder");
            }else{
                MessageBox('提示', '请选择您想要的商品');
            }
        },
         //点击全选或清除
        selectAll(e){
        //获取全选按钮的选中状态
        var rs=e.target.checked;
        //遍历数据所有元素
        for(var item of this.shoplist){
            item.cb=rs;
        }
        //item.cb=全选状态
        //修改全选数据状态
        this.allcb=rs;
        },
        //修改复选框状态  选中 取消
        modifyItem(e){
            //1.获取当前元素选中状态
            var rs=e.target.checked;
            // console.log(rs);
            //2.当前元素在数组中下标
            var i =e.target.dataset.i;
            // console.log(rs+":"+i)
            //3.将状态赋值当前数组下标中对象cb的值
            this.shoplist[i].cb=rs;
            //判断全选复选框状态
            //1.获取数组个数
            var size1=this.shoplist.length;
            //2.计算数组cb true 元素个数
            var count=0;
            for(var item of this.shoplist){
                if(item.cb){
                    count++;
                }
            }
            //3.数组的个数==cb  true  个数[累加和]
            if(count==size1){
                this.allcb=true;
            }else{
                this.allcb=false;
            }
        },
        //订单住址
        addr(){
            var url="addr";
            this.axios.get(url).then(result=>{
                console.log(result.data.code);
                if(result.data.code==-1){
                    MessageBox('您还没有登录,将自动跳转登录页');
                this.$router.push("/login");
                }else{
                this.addrlist=result.data.data[0];
                console.log(this.addrlist)
                }
            })
        },
        //购物车列表
        loadMore(){
            var url="shop";
            this.axios.get(url).then(result=>{
                console.log(result.data.data);
                this.shoplist=result.data.data;
                console.log(this.shoplist);
            })
        }
    }
}
</script>
<style scoped>
/* 修改点击下面的导航颜色 */
    .mui-bar-tab .mui-tab-item.mui-active{
        color: #bed5ab;
    }
    /* 购物车泡泡颜色 */
    .mui-icon .mui-badge{
        background: #bed5ab;
        color: #fff;
    }
    /* 顶部样式 */
.mint-header{
    background-color:  rgba(0, 0, 0, 0.1);
    font-size: 18px;
    margin-bottom:3px;
}
    /* 地址样式 */
.mui-card{
    box-shadow: 0px 1px 10px 1px #d2d1d3;    
}
    /* 装饰 */
.decorate{
    border: 50px solid #bed5ab;
    border-bottom-color: transparent;
    border-top: none;
    border-left-color: transparent;
    position: absolute;
    right: 7px;
    top: 47px;
    z-index: 10;
}
.decorate:before{
    content: '地址';
    position: absolute;
    top: 7px;
    right: -46px;
    font-size: 16px;
    transform: rotateZ(45deg);
    color: #fff;
}
    /* 加入购物车列表 */
.add{
    position: absolute;
    top: 25px;
    left: 60px;
}
.order{
    background: #fff;
    width: 100%;
    position: fixed;
    bottom: 50px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    padding:0px 10px;
    align-items: center;
}
    /* 购物车标题 */
.title{
    color: #000;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.mint-button{
    border-radius: 50px 50px 50px 50px;
    width: 150px;
    margin-left:10px;
    height: 40px ;
    background: #bed5ab;
    color: #fff;
}
.font{
    line-height: 40px;
}
    /* 购物车整个身体 */
.mui-table-view{
    margin-bottom: 45px;
}
    /* 商品列表图片样式 */
.mui-table-view .mui-media-object{
    line-height: 80px;
    max-width: 80px;
    height: 80px;
}
.mui-pull-left{
    clear: both;
    position: relative;
    left: 20px;
}
    /* 复选框样式 */
.check{
    position: relative;
    left: -90px;
    top: 30px;
}
    /* 价格 */
.mui-ellipsis{
    margin-top: 8px;
    margin-left: 5px;
    color: #ef4f4f;
}
.mui-media-body{
    position: relative;
    left: 5%;
}
    /* 删除 */
.del{
    position: absolute;
    top: 45%;
    left: 80%;
}
</style>



