import Vue from 'vue'
import Router from 'vue-router'
import HelloContainer from "./components/HelloWorld.vue"
import Login from "./components/Login.vue"
import Index from "./components/Index.vue"
import Goodlist from "./components/Goodlist.vue"
import Goodinfo from "./components/Goodinfo.vue"
import ShopCart from "./components/ShopCart.vue"
import Self from "./components/Self.vue"
import Merorder from "./components/Merorder.vue"
import Arr from "./components/Arr.vue"
import Insert from "./components/Insert.vue"
import Update from "./components/Update.vue"
import Baike from "./components/Baike.vue"
import Zhuce from "./components/Zhuce.vue"


Vue.use(Router)
//2.为Test组件配置访问路径 /Test
export default new Router({
  routes: [
    {path:'/',redirect:'/Index'},
    {path:'/Index',component:Index},
    {path:'/Login',component:Login},
    {path:'/Goodlist',component:Goodlist},
    {path:'/Goodinfo',component:Goodinfo},
    {path:'/shopcart',component:ShopCart},
    {path:'/self',component:Self},
    {path:'/Merorder',component:Merorder},
    {path:'/arr',component:Arr},
    {path:'/insert',component:Insert},
    {path:'/update',component:Update},
    {path:'/baike',component:Baike},
    {path:'/zhuce',component:Zhuce},
    
  ]
})



