<template>
<div class="app-self">
    <!-- 个人 -->
    <div class="title">
        <div class="zhezao"></div>
        <img src="../img/self2.jpg" class="bg">
        <img src="../img/head.jpg" class="head">
        <p>您好:{{uname}}</p>
    </div>
    <!-- 列表项 -->
    <div class="main">
    <mt-cell title="商品订单" :to="'/Merorder'" is-link></mt-cell>
    <mt-cell title="地址管理" :to="'/arr'" is-link></mt-cell>
    <mt-cell title="宠物百科" :to="'/baike'" is-link></mt-cell>  
    <mt-cell title="联系我们:11223344"></mt-cell>
    </div>
    <!-- 底部 -->
<nav class="mui-bar mui-bar-tab">
    <router-link class="mui-tab-item " :to="'/Index'">
        <span class="mui-icon mui-icon-home"></span>
        <span class="mui-tab-label">首页</span>
    </router-link>
    <router-link class="mui-tab-item" :to="'/Goodlist'">
        <span class="mui-icon mui-icon-extra mui-icon-extra-cate"></span>
        <span class="mui-tab-label">商品</span>
    </router-link>
    <router-link class="mui-tab-item" :to="'/shopcart'">
        <span class="mui-icon mui-icon-extra mui-icon-extra-cart">
            <span class="mui-badge">
                {{$store.getters.optCartCount}} 
            </span>
        </span>
        <span class="mui-tab-label ">购物车</span>
    </router-link>
    <router-link class="mui-tab-item mui-active" :to="'/self'">
        <span class="mui-icon mui-icon-contact"></span>
        <span class="mui-tab-label">个人中心</span>
    </router-link>
</nav>
</div>
</template>
<script>
import {MessageBox} from 'mint-ui'
export default {
    data(){
        return{
            uname:''//当前用户的用户名
        }
    },
    created(){
        this.use()
    },
    methods:{
        use(){
            var url="use";
            this.axios.get(url).then(result=>{
            if(result.data.code==-1){
                MessageBox('您还没有登录,将自动跳转登录页');
            this.$router.push("/login");
            }else{
                console.log(result.data);
                this.uname=result.data.data[0].uname;
            }
            
            })
        },
    }
}
</script>
<style scoped>
/* 修改点击下面的导航颜色 */
    .mui-bar-tab .mui-tab-item.mui-active{
        color: #bed5ab;
    }
    /* 购物车泡泡颜色 */
    .mui-icon .mui-badge{
        background: #bed5ab;
        color: #fff;
    }
    /* 遮罩层 */
.zhezao{
   background: rgba(0, 0, 0, 0.2);
   position: absolute;
}
.bg,.zhezao{
    width: 100%;
    height: 265px;
}
    /* 头像 */
.head{
    border-radius: 50%;
    width: 100px;
    height: 100px;
    position: absolute;
    top: 60px;
    left: 35%;
}
    /* 用户名 */
p{
    position: absolute;
    top: 170px;
    left: 39%;
    color: #fff;
}
    /* 内容 */
.main{
    margin-top: 5px;
    box-shadow: 0px 1px 10px 1px #d2d1d3;
}
</style>



