<template>
<div class="app-order ">
    <!-- 表头 -->
  <mt-header title="商品订单">
  <router-link :to="'/self'" slot="left">
    <mt-button icon="back"></mt-button>
  </router-link>
</mt-header>
    <!-- 选项卡 -->
<mt-navbar v-model="selected">
  <mt-tab-item id="1">待付款</mt-tab-item>
  <mt-tab-item id="2">待发货</mt-tab-item>
  <mt-tab-item id="3">待收货</mt-tab-item>
  <mt-tab-item id="4">退款</mt-tab-item>
</mt-navbar>
<!-- tab-container -->
<mt-tab-container v-model="selected">
    <!-- 待付款 -->
  <mt-tab-container-item id="1">
    <div class="topay">
        <img src="../img/wait.png">
       <div> 还没有相关的订单</div>
    </div>
  </mt-tab-container-item>
  <!-- 待发货 -->
  <mt-tab-container-item id="2">
<div class="mui-card">
    <div class="mui-card-content">
        <div class="mui-card-content-inner">
            <img src="../img/wait2.png">
            <div>
            <p>待发货</p>
            <p class='mui-ellipsis'>买家已付款,等待商家发货</p></div>
        </div>
    </div>
</div>
   <ul class="mui-table-view">
    <li class="mui-table-view-cell mui-media" v-for="(item,i) of shoplist" :key=i>
        <img class="mui-media-object mui-pull-left" :src="''+item.pic">
        <div class="mui-media-body">
            {{item.pname}}
            <p class='mui-ellipsis'>¥{{item.price}}</p>
           <button class="btn" @click="remind">提醒发货</button>
        </div>
    </li>
</ul>
  </mt-tab-container-item>
  <!-- 待收货 -->
  <mt-tab-container-item id="3">
    <ul class="mui-table-view">
    <li class="mui-table-view-cell mui-media" v-for="(item,i) of shoplist" :key=i>
        <img class="mui-media-object mui-pull-left" :src="''+item.pic">
        <div class="mui-media-body">
            {{item.pname}}
            <p class='mui-ellipsis'>¥{{item.price}}</p>
        </div>
        <div class="sum">
            共1件商品 合计:{{item.price.toFixed(2)}}(含运费¥10)
        </div>
        <button class="btn" @click="remind">确认收货</button>
    </li>
</ul>
  </mt-tab-container-item>
  <!-- 退款 -->
  <mt-tab-container-item id="4">
      <ul class="mui-table-view">
    <li class="mui-table-view-cell mui-media" v-for="(item,i) of shoplist" :key=i>
        <img class="mui-media-object mui-pull-left" :src="''+item.pic">
        <div class="mui-media-body">
            {{item.pname}}
            <p class='mui-ellipsis'>¥{{item.price}}</p>
           <button class="btn" @click="refund">退款</button>
        </div>
    </li>
</ul>
  </mt-tab-container-item>
</mt-tab-container>
</div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return{
            selected:'1',    //选项卡
            uid:1,
            shoplist:[]
        }
    },
    created(){
        this.loadMore()
    },
    methods:{
        //购物车列表
        loadMore(){
            var url="shop?uid="+this.uid;
            this.axios.get(url).then(result=>{
                // console.log(result.data.data);
                this.shoplist=result.data.data;
                console.log(this.shoplist);
            })
        },
        //提醒发货
        remind(){
             Toast("您的快递会尽快安排上...");
        },
        //退款
        refund(){
            Toast("亲,我们会在24小时之内及时处理")
        }
    }
}
</script>
<style scoped>
    /* 选项卡点击显示的颜色 */
.mint-navbar .mint-tab-item.is-selected{
    color: #bed5ab;
    border-bottom:3px solid  #bed5ab;
}
/*表头*/
.mint-header{
background: rgba(0, 0, 0, 0.1);
    color: #fff;
}
    /* 选项卡样式 */
a{color: #000;}
    /* 待付款 */
.topay{
    display: block;
    text-align: center;
    margin-top: 110px;
}
    /* 待发货 */
.mui-table-view{
    margin-top: 10px;
}
.mui-card-content-inner{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.mui-card-content-inner div p:nth-child(1){
    font-size: 16px;
    color: #000;
}
    /* 提醒发货 */
.btn{
    float: right;
    margin-top: 5px;
}
    /* 待收货 */
.sum{
    width: 100%;
    padding: 10px 0;
    padding-left:80px; 
    /* border-bottom: 1px solid #e8e8e8; */
    font-size: 16px;
    color: #949499;
}
</style>



