<template>
    <div class="app-home">
        <!-- 轮播图 -->
        <mt-swipe :auto="4000" class="mt-swipe">
           <mt-swipe-item v-for="(item,i) of navlist" :key="i">
               <img :src="item.pic">
               </mt-swipe-item>
       </mt-swipe>
       <!-- 九宫格 -->
        <ul class="mui-table-view mui-grid-view mui-grid-9">
            <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3" v-for="(item,i) of gridlist " :key="i">
                <router-link :to="'/Goodlist'">
                    <img :src="item.img" :data-id="item.lid">
                    <div class="mui-media-body">{{item.title}}</div>
                </router-link></li>
        </ul>
       <!-- 选项卡 -->
<mt-navbar v-model="selected">
  <mt-tab-item id="1">宠物秀</mt-tab-item>
  <mt-tab-item id="2">医院</mt-tab-item>
</mt-navbar>
<!-- tab-container -->
<mt-tab-container v-model="selected">
  <mt-tab-container-item id="1">
    <div v-for="(item,i) of showlist " :key="i" id="show_img">
            <img :src= "item.img">
            <h5>{{item.title}}</h5>
    </div>
  </mt-tab-container-item>
  <mt-tab-container-item id="2" class="hos">
    <div class="item" v-for="(item,i) of hospitalist" :key="i">
            <img :src="item.img">
            <h5>{{item.title}}</h5>
            <p>{{item.tel}}</p>
        </div>
  </mt-tab-container-item>
</mt-tab-container>
<!-- 底部 -->
<nav class="mui-bar mui-bar-tab">
    <router-link class="mui-tab-item mui-active" :to="'/Index'">
        <span class="mui-icon mui-icon-home"></span>
        <span class="mui-tab-label">首页</span>
    </router-link>
    <router-link class="mui-tab-item" :to="'/Goodlist'">
        <span class="mui-icon mui-icon-extra mui-icon-extra-cate"></span>
        <span class="mui-tab-label">商品</span>
    </router-link>
    <router-link class="mui-tab-item" :to="'/shopcart'">
        <span class="mui-icon mui-icon-extra mui-icon-extra-cart">
            <span class="mui-badge">
                {{$store.getters.optCartCount}} 
            </span>
        </span>
        <span class="mui-tab-label ">购物车</span>
    </router-link>
    <router-link class="mui-tab-item" :to="'/self'">
        <span class="mui-icon mui-icon-contact"></span>
        <span class="mui-tab-label">个人中心</span>
    </router-link>
</nav>
    </div>
</template>
<script>
export default {
    data(){
        return{
            selected:'1',//选项卡
            navlist:[],//轮播图列表
            gridlist:[],//九宫格数组
            showlist:[],//宠物秀数组
            hospitalist:[],//宠物医院
        }
    },
    created(){
        this.Image();
        this.grid();
        this.show();
        this.hospital();
    },
    methods:{
        // 轮播图加载数据
        Image(){
            var url="imglist";
            this.axios.get(url).then(result=>{
            //  console.log(result.data);
                this.navlist=result.data;
            //    console.log(this.navlist);
            })
        },
        //九宫格效果
        grid(){
            var url="grid";
            this.axios.get(url).then(result=>{
                // console.log(result.data);
                this.gridlist=result.data;
            })
        },
        //宠物秀
        show(){
            var url="show";
            this.axios.get(url).then(result=>{
                // console.log(result.data);
                this.showlist=result.data;
            })
        },
        //宠物医院
        hospital(){
var url="hospital";
this.axios.get(url).then(result=>{
    // console.log(result.data);
    this.hospitalist=result.data;
})
        }
    }
}
</script>
<style scoped>
/* 修改点击下面的导航颜色 */
    .mui-bar-tab .mui-tab-item.mui-active{
        color: #bed5ab;
    }
    /* 购物车泡泡颜色 */
    .mui-icon .mui-badge{
        background: #bed5ab;
        color: #fff;
    }
 /* 选项卡点击显示的颜色 */
.mint-navbar .mint-tab-item.is-selected{
    color: #bed5ab;
    border-bottom:3px solid  #bed5ab;
}
/*轮播*/
.app-home .mint-swipe{
        height: 200px;
    }
    .app-home .mint-swipe img{
        width: 100%;
    }
/*九宫格图片的宽度 逻辑像素*/
.app-home .mui-grid-9 img{
    width: 60px;
    height: 60px;
}
/*选项卡标题*/
a{
    color: #000;
}
/*宠物秀图片*/
img{
    height: 210px;
    width: 100%;
    border-radius: 5px 5px;    
}
h5{
    text-align: center;
    color: #d68c05;
    font-family: FangSong;
}
.mint-tab-container{
    margin-top: 10px;
}
/*宠物医院*/
.hos{
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
}
.item{
    width: 50%;
    padding: 0 10px;
    box-sizing: border-box;
}
.item img{
    width: 100%;
    height: 140px;
    border-radius: 5px 5px;
}
.item:nth-child(odd){
     padding-right:0; 
}
.item h5{
    color: #d68c05;
}
.item h5,.item p{
    text-align: center;
}
.item p{
    position: relative;
    top: 10px;
}

</style>



