<template>
<div class="app-arr">
     <!-- 表头 -->
  <mt-header title="我的收货地址">
  <router-link :to="'/self'" slot="left">
    <mt-button icon="back"></mt-button>
  </router-link>
</mt-header>
    <!-- 住址列表 -->
<div class="mui-card">
    <div class="mui-card-content">
        <div class="mui-card-content-inner" v-for="(item,i) of addrlist" :key=i>
            <p>
                <span>{{item.name}}  </span>
                <span>{{item.tel}}</span>
            </p>
            <p>{{item.place}}</p>
        <div class="update">
            <div @click="del">
                <span class="mui-icon mui-icon-trash"></span>
                <span :data-id="item.id">删除</span>
            </div>
            <div @click="update">
                <span class="mui-icon mui-icon-compose"></span>
                <span :data-id="item.id">编辑</span>
            </div>
            
        </div>    
        </div>
    </div>
</div>
    <!-- 添加住址 -->
    <button @click="add" class="btn">添加住址</button>
</div>    
</template>
<script>
export default {
    data(){
        return{
            addrlist:[],//住址
        }
    },
    created(){
        this.addr();
    },
    methods:{
        //订单住址
        addr(){
            var url="allr";
            this.axios.get(url).then(result=>{
                // console.log(result.data);
                this.addrlist=result.data.data;
                console.log(this.addrlist)
            })
        },
        // 删除住址
        del(e){
            var id=e.target.dataset.id;
            console.log(id);
            var url="delr?id="+id;
            this.axios.get(url).then(result=>{
                // console.log(result.data.code);
                if(result.data.code==1){
                    this.addr();
                }
            })
        },
        // 添加住址
        add(){
            this.$router.push("/insert");
        },
        // 修改
        update(e){
            var id=e.target.dataset.id;
            console.log(id);
            this.$router.push("/update?id="+id);
        }
    }
}
</script>
<style scoped>
/*表头*/
.mint-header{
background: rgba(0, 0, 0, 0.1);
}
    /* 住址样式 */
.mui-card-content-inner p:nth-child(2){
    padding-bottom: 10px;
    border-bottom: 1px solid #e8e8e8;
}
a{
    color: #000;
}
    /* 删除  编辑 */
.update{
    display: flex;
    flex-direction: row-reverse;
}
.update div{
    padding-left:15px; 
}
.update div:nth-child(1){
    padding-left:60%; 
    padding-right:2%; 
} 
.update div span:nth-child(2){
    color: #bed5ab;
}
    /* 添加按钮 */
.btn{
    margin-top: 10px;
    width: 100%;
    border-radius: 50px 50px 50px 50px;
    background: #bed5ab;
    color: #fff;
}
</style>



