<template>
    <div class="app-zhuce">
        <div id="title">
        <img src="../img/IMG_3779.png">
        <h1>注册</h1>
        </div>
        <input type="text" name="uname" v-model="uname" placeholder="请输入用户名" class="inp">
            <br>
        <input type="password" name="upwd" v-model="upwd" placeholder="请输入密码" class="inp">
            <br>
        <input type="text" name="uname" v-model="phone" placeholder="请输入手机号" class="inp">
            
    <mt-button @click="btn" size="large">注册</mt-button>
    </div>
</template>
<script>
import {Toast} from 'mint-ui'
import {MessageBox} from 'mint-ui'
export default {
    data(){
        return{
            uname:'',
            upwd:'',
            phone:''
        }
    },
    methods:{
        btn(){
            var uname=this.uname;
            var upwd=this.upwd;
            var phone=this.phone;
            var reg1=/^.{6,15}$/;
            if(!reg1.test(upwd)){
                Toast("您的密码格式不正确");
                return;
            }
            var reg2=/^((\+86|0086)\s+)?1[3-8]\d{9}$/;
            if(!reg2.test(phone)){
                Toast("您的手机格式不正确");
                return;
            }
            var postData=this.qs.stringify({
                uname:uname,
                upwd:upwd,
                phone:phone
            })
            console.log(postData);
            var url="zhuce";
            this.axios.post(url,postData).then(result=>{
                console.log(result.data.code);
                if(result.data.code==1){
                MessageBox('注册成功', '请登录');
                this.$router.push("/login");
                }
            })
        }
    }
}
</script>
<style scoped>
#title{
    text-align: center;
    margin-bottom: 30px;
}
.inp{
    border:none;
    background: #efeff4;
    border-bottom: 1px solid #aaa9a9;
}
.mint-button--large{
    background: #bed5ab;
    color: #fff;
}
</style>



