<template>
    <div class="app-login">
        <div id="title">
        <img src="../img/IMG_3779.png">
        <h1>欢迎登录</h1>
        </div>
        <input type="text" name="uname" v-model="uname" placeholder="请输入用户名" class="inp">
            <br>
        <input type="password" name="upwd" v-model="upwd" placeholder="请输入密码" class="inp">
            <br>
        <router-link :to="'/zhuce'" class="zhuce">注册</router-link>
            
    <mt-button @click="btnClick" size="large">登录</mt-button>
    </div>
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return {
            uname:"",
            upwd:""
        }
    },
    methods:{
        btnClick(){
            var uname=this.uname;
            var p=this.upwd;
            console.log(uname+":"+p);
            var reg=/^[a-z0-9_]{3,8}$/i;
            if(!reg.test(uname)){
                Toast("用户名格式不正确,请检查")
                return;
            }
            if(!reg.test(p)){
                Toast("密码格式不正确,请检查")
                return;
            }
            var postData=this.qs.stringify({
                uname:uname,
                upwd:p
            })
            console.log(postData);
            //发送请求
            var url="";
            url+="/login";
            this.axios.post(url,postData).then(result=>{
                console.log(result.data)
                if(result.data.code==1){
                    Toast("登录成功,进入购物车");
                    this.$router.push("/shopcart");
                }
            })
        }
    }
}
</script>
<style scoped>
    /* 标题 */
#title{
    text-align: center;
    margin-bottom: 30px;
}
    /* 输入框 */
.inp{
    border:none;
    background: #efeff4;
    border-bottom: 1px solid #aaa9a9;
}
    /* 登录 */
.mint-button--large{
    background: #bed5ab;
    color: #fff;
    margin-top: 5px;
}
.zhuce{
    padding-left: 85%;
    color: #000;
}
</style>



